import React from "react";
import {
  MDBCol,
  MDBContainer,
  MDBInput,
  MDBRow,
  MDBTypography,
} from "mdb-react-ui-kit";
import Company from "../../fakeDb/company";
import List from "./list";
import Clock from "./clock";
import "./clock.css";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { useState } from "react";
import { BROWSE, SAVE } from "../../redux/slices/attendances";
import { ENDPOINT, fullName, PresetUser } from "../utilities";
import axios from "axios";
export default function DTR() {
  const date = new Date(),
    dispatch = useDispatch(),
    { catalogs } = useSelector(({ attendances }) => attendances),
    [id, setId] = useState("");

  useEffect(() => {
    dispatch(BROWSE());
  }, [catalogs]);

  const handleLogin = async () => {
    await dispatch(SAVE({ form: { id } }));
    var { out, email, sectionId, parent, role } = catalogs[0].userId;
    if (role.name === "student")
      axios.post("mailer/sendMsg", {
        student: fullName(catalogs[0].userId.fullName),
        status: `${out ? "out" : "in"}`,
        sectionId,
        email,
        parentEmail: parent.email,
      });
  };

  return (
    <MDBRow style={{ height: "100vh" }} className="overflow-hidden">
      <List items={catalogs.slice(1)} />
      <MDBCol md={9} size={8} className="px-0">
        <MDBCol
          md={12}
          size={3}
          className="pb-4"
          style={{ height: "60vh", backgroundColor: "#e4e4e4" }}
        >
          <MDBRow>
            <MDBCol md={3} size={3} className="text-center">
              <img
                src={`${ENDPOINT}/assets/profile/${catalogs[0]?.userId?.email}.png`}
                alt="..."
                onError={e => (e.target.src = PresetUser)}
                height="300px"
                width="300px"
                className="mb-4"
              />
              <MDBTypography tag={"h2"}>
                {catalogs[0]?.userId?.fullName?.fname}
              </MDBTypography>
              <MDBInput
                label="Student Id"
                onChange={e => setId(e.target.value)}
                onKeyDown={e => {
                  if (e.key === "Enter") {
                    handleLogin();
                  }
                }}
                className="mt-4"
                type="text"
              />
            </MDBCol>
            <MDBCol md={9} size={3} className="h-100 text-center">
              <Clock />
              <MDBTypography tag="h1">{Company.name}</MDBTypography>
              <MDBTypography tag="h2">{date.toDateString()}</MDBTypography>
            </MDBCol>
          </MDBRow>
        </MDBCol>
        <MDBCol
          md={12}
          size={3}
          className="d-flex  px-4 pb-4 bg-info"
          style={{ height: "40vh" }}
        >
          <MDBContainer className="rounded text-center d-grid align-items-center">
            <h1 style={{ fontSize: "70px" }}>Event:X-mas party</h1>
          </MDBContainer>
        </MDBCol>
      </MDBCol>
    </MDBRow>
  );
}
