import React from "react";
import { MDBBadge, MDBBtn, MDBIcon, MDBTooltip } from "mdb-react-ui-kit";
import Swal from "sweetalert2";
import { useDispatch, useSelector } from "react-redux";
import { DESTROY } from "../../../../../../redux/slices/sections";
import { fullName } from "../../../../../../components/utilities";

export default function TableCard({ item, setData }) {
  const { token } = useSelector(({ auth }) => auth),
    dispatch = useDispatch();

  const handleArchives = () =>
    Swal.fire({
      icon: "question",
      title: `Do you want to archive this?`,
      html: item.name,
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then(result => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        dispatch(DESTROY({ id: item._id, token }));
      }
    });

  return (
    <tr>
      <td>
        <span style={{ textDecoration: item.isComplete && "line-through" }}>
          {item.name}
        </span>
      </td>
      <td>
        <span style={{ textDecoration: item.isComplete && "line-through" }}>
          {item?.hasPermission ? (
            <MDBBadge color="success" pill>
              Has permission
            </MDBBadge>
          ) : (
            <MDBBadge color="danger" pill>
              No permmission
            </MDBBadge>
          )}
        </span>
      </td>
      <td>
        <span style={{ textDecoration: item.isComplete && "line-through" }}>
          {item.teacherId.isMale ? "Mr. " : "Ms. "}
          {fullName(item?.teacherId?.fullName)}
        </span>
      </td>
      <td className="text-center">
        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title="Update task!"
        >
          <MDBBtn onClick={() => setData(item)} size="sm" color="info">
            <MDBIcon icon="pen" />
          </MDBBtn>
        </MDBTooltip>

        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title="Archive task!"
        >
          <MDBBtn size="sm" color="warning" onClick={handleArchives}>
            <MDBIcon icon="trash-alt" />
          </MDBBtn>
        </MDBTooltip>
      </td>
    </tr>
  );
}
