import React, { useState, useEffect } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBSpinner,
} from "mdb-react-ui-kit";
import { useDispatch, useSelector } from "react-redux";
import ModalForm from "./form";
import { REVERT, UPDATE } from "../../../../../redux/slices/users";
import { SAVE as SAVE_USER } from "../../../../../redux/slices/auth";
import { BROWSE } from "../../../../../redux/slices/sections";
import { SAVE as NOTIFY } from "../../../../../redux/slices/notifications";
import { useParams } from "react-router-dom";

export default function ProductModal({
  setVisibility,
  visibility,
  data,
  setData,
  list,
}) {
  const { theme, token } = useSelector(({ auth }) => auth),
    { isSuccess } = useSelector(({ users }) => users),
    [fullName, setFullName] = useState({
      fname: "",
      mname: "",
      lname: "",
      suffix: "",
    }),
    [parents, setParent] = useState({
      email: "",
      mobile: "",
    }),
    [form, setForm] = useState({
      nickname: "",
      dob: "",
      isMale: true,
      sectionId: "",
      address: "",
      email: "",
      base64: undefined,
      lrn: "",
      mobile: "",
      rate: 0,
    }),
    [base64, setBase64] = useState({
      base64: "",
      name: "",
    }),
    [loading, setLoading] = useState(false),
    { type } = useParams(),
    dispatch = useDispatch();

  useEffect(() => {
    if (data && data._id) {
      setForm(data);
      setFullName(data.fullName);
      setParent(data.parents);
    }
  }, [data]);

  const toggleShow = () => setVisibility(!visibility);

  useEffect(() => {
    dispatch(BROWSE(token));
    if (isSuccess) {
      if (data && data._id) {
        setData({});
      }
      dispatch(REVERT());
      setParent({
        email: "",
        mobile: "",
      });
      setFullName({
        fname: "",
        mname: "",
        lname: "",
        suffix: "",
      });
      setForm({
        nickname: "",
        dob: "",
        isMale: true,
        address: "",
        email: "",
        base64: undefined,
        lrn: "",
        sectionId: "",
        mobile: "",
        rate: 0,
      });
      setLoading(false);
      if (visibility) {
        toggleShow();
      }
    }
  }, [isSuccess, data, visibility]);

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);

    const newObj = { ...form };
    newObj.fullName = fullName;
    newObj.parents = parents;
    newObj.base64 = base64.base64;
    newObj.name = base64.name;
    console.log(newObj);

    if (data && data._id) {
      if (newObj.rate !== data.rate) {
        dispatch(
          NOTIFY({
            form: {
              userId: data._id,
              content: `Hourly rate changed from ${data.rate} to ${newObj.rate}`,
              icon:
                data.rate > newObj.rate
                  ? "angle-double-down"
                  : "angle-double-up",
              color: data.rate > newObj.rate ? "danger" : "success",
            },
            token,
          })
        );
      }
      dispatch(
        UPDATE({
          data: newObj,
          id: data._id,
          token,
        })
      );
    } else {
      newObj.password = "password";
      newObj.role = {
        name: type,
        display_name: type.toUpperCase(),
      };
      dispatch(SAVE_USER({ form: newObj, token }));
    }
  };

  const handleForm = (name, value) => {
    setForm({
      ...form,
      [name]: value,
    });
  };

  const handleFullName = (name, value) => {
    setFullName({
      ...fullName,
      [name]: value,
    });
  };

  const handleParents = (name, value) => {
    setParent({
      ...parents,
      [name]: value,
    });
  };
  const handleFile = file => {
    if (file.name.split(".").pop() !== "png") {
    }
    const reader = new FileReader();
    reader.onload = async () => {
      setBase64({
        base64: reader.result.split(",")[1],
        name: file.name.split(".").pop(),
      });
      // handleForm("base64", reader.result.split(",")[1]);
      // handleForm("name", file.name.split(".").pop());
    };
    reader.readAsDataURL(file);
    alert("file.name");
  };

  const handleChangeProfile = () =>
    document.getElementById("uploadProfile").click();
  return (
    <MDBModal tabIndex="-1" show={visibility} staticBackdrop>
      <MDBModalDialog centered size="lg">
        <MDBModalContent className={`${theme.bg} ${theme.text}`}>
          <MDBModalHeader>
            <MDBModalTitle>Register a stundent</MDBModalTitle>
          </MDBModalHeader>
          <form onSubmit={handleSubmit} autoComplete="off">
            <MDBModalBody>
              <ModalForm
                sections={list}
                form={form}
                parents={parents}
                fullName={fullName}
                handleForm={handleForm}
                handleFile={handleFile}
                handleFullName={handleFullName}
                handleParents={handleParents}
                handleChangeProfile={handleChangeProfile}
              />
            </MDBModalBody>
            <MDBModalFooter className="py-0">
              <MDBBtn
                type="button"
                className="shadow-0"
                color={theme.color}
                onClick={() => {
                  if (data && data._id) {
                    setData({});
                  }
                  setParent({
                    email: "",
                    mobile: "",
                  });
                  setFullName({
                    fname: "",
                    mname: "",
                    lname: "",
                    suffix: "",
                  });
                  setForm({
                    nickname: "",
                    dob: "",
                    isMale: true,
                    address: "",
                    email: "",
                    sectionId: "",
                    base64: undefined,
                    mobile: "",
                    lrn: "",
                    rate: 0,
                  });
                  toggleShow();
                }}
                disabled={loading}
              >
                Close
              </MDBBtn>
              <MDBBtn type="submit" disabled={loading}>
                {loading ? <MDBSpinner size="sm" grow /> : "Submit"}
              </MDBBtn>
            </MDBModalFooter>
          </form>
        </MDBModalContent>
      </MDBModalDialog>
    </MDBModal>
  );
}
