import React from "react";
import { useParams } from "react-router-dom";
import Barcode from "react-barcode";
import { useDispatch } from "react-redux";
import { SAVE } from "../../redux/slices/users";
import { ENDPOINT } from "../utilities";
import { useState } from "react";
import axios from "axios";
export default function QrGenerator() {
  const { email, id } = useParams(),
    [barcode, setBarcode] = useState(),
    dispatch = useDispatch();

  const handleDlQr = async download => {
    await dispatch(
      SAVE({
        email,
        data: document.getElementsByTagName("svg")[0].outerHTML,
      })
    );
    let img = new Image();
    img.src = `${ENDPOINT}/assets/barcode/${email}/${email}barcode.png`;
    img.crossOrigin = "anonymous";
    img.onload = () => {
      //get image to backend to download
      let canvas = document.createElement("canvas");
      canvas.width = img.width;
      canvas.height = img.height;
      let ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0);
      let dataURL = canvas.toDataURL("image/png");
      //Download
      if (download) {
        const a = document.createElement("a");
        a.style.display = "none";
        a.href = `${dataURL}`;
        a.download = `${email}.png`;
        document.body.appendChild(a);
        a.click();
        setBarcode(dataURL);
      }
    };

    axios.post("mailer/sendBarcode", {
      email,
    });
  };
  return (
    <div className="d-flex justify-content-center vh-100" id="qr">
      <div className="d-row ">
        <Barcode value={id} className="my-auto" id="qr" />
        <button
          className="btn btn-success mt-5 d-block w-100"
          onClick={() => handleDlQr(false)}
        >
          Send
        </button>
        <button
          className="btn btn-info mt-2 d-block w-100"
          onClick={() => handleDlQr(true)}
        >
          Download
        </button>
      </div>
    </div>
  );
}
