import { MDBIcon, MDBTooltip } from "mdb-react-ui-kit";
import React from "react";
import { formatCurrency } from "../../utilities";

export default function TableCard({ item, action }) {
  return (
    <tr>
      <td className="text-start py-0 px-1">
        <p className="mb-0 text-capitalize">{item.productId?.name}</p>
      </td>
      <td className="px-1 py-0 text-start">{item.quantity}</td>
      {action !== "print" && (
        <td className="py-0">
          {item.overridden ? (
            <MDBTooltip
              tag="span"
              wrapperClass="d-inline-block"
              title={`Price were set ${
                item.overridden > item.price ? "lower" : "higher"
              } than initial price`}
            >
              {formatCurrency(item.overridden)}
              <MDBIcon
                icon="caret-right"
                className="mx-2"
                color={item.overridden > item.price ? "danger" : "success"}
              />
              {formatCurrency(item.price)}
            </MDBTooltip>
          ) : (
            formatCurrency(item.price)
          )}
        </td>
      )}
      <td className="px-1 text-end py-0">
        {formatCurrency(item.price * item.quantity)}
      </td>
    </tr>
  );
}
