import React from "react";
import { MDBCol, MDBInput, MDBRow, MDBSwitch } from "mdb-react-ui-kit";
import { useSelector } from "react-redux";

export default function ModalForm({ form, handleForm }) {
  const { theme } = useSelector(({ auth }) => auth);
  return (
    <MDBRow className="my-3">
      <MDBCol size={12}>
        <MDBInput
          label="Name"
          value={form.name}
          onChange={e => handleForm("name", e.target.value)}
          required
          onInvalid={e => e.target.setCustomValidity("We name for this task.")}
          onInput={e => e.target.setCustomValidity("")}
          contrast={theme.dark}
        />
      </MDBCol>
      <MDBCol size={4} className="d-flex align-items-center mt-4">
        <MDBSwitch
          onChange={() => handleForm("hasPermission", !form.hasPermission)}
          id="hasPermission"
          label={form.hasPermission ? "Permit" : "Avoid"}
          checked={form.hasPermission}
        />
      </MDBCol>
    </MDBRow>
  );
}
