import Manager from "./dashboard";
import Users from "./users";
import Sections from "./sections";

const routes = {
  path: "manager",
  children: [
    {
      path: "dashboard",
      element: <Manager />,
    },
    {
      path: "users/:type",
      element: <Users />,
    },
    {
      path: "sections",
      element: <Sections />,
    },
  ],
};

export default routes;
