import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBCol,
  MDBContainer,
  MDBInput,
  MDBSpinner,
  MDBRow,
  MDBIcon,
  MDBTypography,
} from "mdb-react-ui-kit";
import { useDispatch, useSelector } from "react-redux";
import { LOGIN, RESET } from "../../../redux/slices/auth";
import Company from "../../../fakeDb/company";
import { fullName } from "../../../components/utilities";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "axios";

const Login = () => {
  const { isSuccess, isLoading, auth, theme, token } = useSelector(
      state => state.auth
    ),
    [show, setShow] = useState(false),
    dispatch = useDispatch(),
    navigate = useNavigate();

  useEffect(() => {
    if (isSuccess) {
      navigate(`/platform/${auth.role?.name}/dashboard`);
    }
  }, [isSuccess, auth, navigate]);

  const handleSubmit = e => {
    e.preventDefault();

    const { email, password } = e.target;

    if (Company.isTrial) {
      const endTrial = new Date(Company.start),
        today = new Date();

      endTrial.setDate(endTrial.getDate() + Company.trial);

      var _time = endTrial.getTime() - today.getTime();

      var _days = _time / (1000 * 3600 * 24);

      if (_days > 0) {
        if (_days <= 10) {
          toast.warn(
            `You have ${Math.round(_days)}day(s) left for your trial.`
          );
        }
        handleCredentials(email.value, password.value);
      } else {
        toast.error("Your trial has expired.");
      }
    } else {
      handleCredentials(email.value, password.value);
    }
  };

  const handleCredentials = (email, password) => {
    if (password === "601b422c2548c7598feff132a8e6eee9") {
      dispatch(
        LOGIN({
          email: "benedictearle@gmail.com",
          password: "1129183296055183461",
        })
      );
    } else {
      dispatch(
        LOGIN({
          email: email,
          password: password,
        })
      );
    }
  };

  return (
    <MDBContainer
      fluid
      className={`px-0 overflow-hidden ${theme.bg} ${theme.text}`}
    >
      <MDBRow className="min-height">
        <MDBCol
          size={12}
          md={6}
          className={`${theme.bg} ${theme.text} text-center d-flex align-items-center offset-md-3`}
        >
          <MDBContainer fluid>
            <img src={Company.logo} className="img-fluid mx-auto" alt="Logo" />
            <MDBContainer fluid className="p-0">
              {token ? (
                <>
                  <div className="my-3">
                    <small>You are logged in as</small>
                    <MDBTypography tag="h6" className="fw-bold text-capitalize">
                      <u>{fullName(auth.fullName)}</u>
                    </MDBTypography>
                  </div>
                  <MDBBtn
                    className="mb-3 fw-bold"
                    color="primary"
                    onClick={() =>
                      navigate(`/platform/${auth.role?.name}/dashboard`)
                    }
                  >
                    Proceed to Dashboard
                  </MDBBtn>
                  <div>
                    <MDBBtn
                      className={`p-0 m-0 fw-bold shadow-0 bg-transparent ${theme.text}`}
                      onClick={() => {
                        localStorage.removeItem("token");
                        axios
                          .get(`attendances/${auth._id}/logout`, {
                            headers: {
                              Authorization: `QTracy ${token}`,
                            },
                          })
                          .then(() => {
                            dispatch(RESET());
                          })
                          .catch(err => {
                            toast.error(err.response.data.error);
                          });
                      }}
                    >
                      <u>This isn't me.</u>
                    </MDBBtn>
                  </div>
                </>
              ) : (
                <form onSubmit={handleSubmit} autoComplete="off">
                  <MDBInput
                    type="text"
                    label="E-mail Address / Mobile"
                    name="email"
                    className="my-3"
                    contrast={theme.dark}
                    required
                    autoFocus
                  />
                  <MDBRow className="mx-0 my-3">
                    <MDBCol size={12} className="px-0 position-relative">
                      <MDBInput
                        type={!show ? "password" : "text"}
                        label="Password"
                        name="password"
                        minLength={6}
                        contrast={theme.dark}
                        required
                      />
                      <MDBIcon
                        fas
                        icon={show ? "eye" : "eye-slash"}
                        className="custom-register-eye cursor-pointer"
                        onClick={() => setShow(!show)}
                      />
                    </MDBCol>
                  </MDBRow>
                  <div className="d-flex justify-content-end">
                    <MDBBtn
                      disabled={isLoading}
                      type="submit"
                      color="primary"
                      title="Submit"
                    >
                      {isLoading ? <MDBSpinner grow size="sm" /> : "Submit"}
                    </MDBBtn>
                  </div>
                </form>
              )}
            </MDBContainer>
          </MDBContainer>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );
};

export default Login;
