import React from "react";
import {
  MDBBtnGroup,
  MDBCol,
  MDBIcon,
  MDBInput,
  MDBInputGroup,
  MDBRow,
  MDBSwitch,
  MDBTextArea,
  MDBTooltip,
} from "mdb-react-ui-kit";
import { useSelector } from "react-redux";
import { ENDPOINT, PresetUser } from "../../../../../components/utilities";

export default function ModalForm({
  parents,
  form,
  handleForm,
  fullName,
  handleFullName,
  handleFile,
  sections,
  handleChangeProfile,
  handleParents,
}) {
  const { theme } = useSelector(({ auth }) => auth);
  return (
    <>
      <MDBRow>
        <MDBCol size={3}>
          <MDBInput
            label="First name"
            value={fullName.fname}
            onChange={e => handleFullName("fname", e.target.value)}
            required
            onInvalid={e =>
              e.target.setCustomValidity("Surely they have a First name.")
            }
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={3}>
          <MDBInput
            label="Middle name (Optional)"
            value={fullName.mname}
            onChange={e => handleFullName("mname", e.target.value)}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={3}>
          <MDBInput
            label="Last name"
            value={fullName.lname}
            onChange={e => handleFullName("lname", e.target.value)}
            required
            onInvalid={e =>
              e.target.setCustomValidity("Everyone has a Last name.")
            }
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={3}>
          <MDBInputGroup textBefore={<span className={theme.text}>EXT</span>}>
            <select
              value={fullName.suffix}
              onChange={e => handleFullName("suffix", e.target.value)}
              className={`form-control ${theme.bg} ${theme.text}`}
            >
              <option value="NONE">NONE</option>
              <option value="JR">JR</option>
              <option value="SR">SR</option>
              <option value="III">III</option>
              <option value="IV">IV</option>
              <option value="V">V</option>
            </select>
          </MDBInputGroup>
        </MDBCol>
      </MDBRow>
      <MDBRow className="my-3">
        <MDBCol size={4}>
          <MDBInput
            label="Nickname (Optional)"
            value={form.nickname}
            onChange={e => handleForm("nickname", e.target.value)}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={4}>
          <MDBInput
            label="Date of Birth"
            value={form.dob}
            type="date"
            onChange={e => handleForm("dob", e.target.value)}
            required
            onInvalid={e =>
              e.target.setCustomValidity("How can we celebrate their birthday?")
            }
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={4} className="d-flex align-items-center">
          <MDBSwitch
            onChange={() => handleForm("isMale", !form.isMale)}
            id="isMale"
            label={form.isMale ? "Male" : "Female"}
            checked={form.isMale}
          />
        </MDBCol>
      </MDBRow>
      <MDBCol size={12}>
        <MDBTextArea
          label="Full Address"
          value={form.address}
          onChange={e => handleForm("address", e.target.value)}
          onInvalid={e =>
            e.target.setCustomValidity(
              "How can we visit them on their birthday?"
            )
          }
          onInput={e => e.target.setCustomValidity("")}
          required
          contrast={theme.dark}
        />
      </MDBCol>
      <MDBRow className="my-3">
        <MDBCol size={4}>
          <MDBInput
            type="email"
            label="E-mail Address"
            value={form.email}
            required
            onChange={e => handleForm("email", e.target.value)}
            onInvalid={e => e.target.setCustomValidity("Any email will do.")}
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={4}>
          <MDBInput
            label="Mobile Number"
            value={form.mobile}
            required
            onChange={e => handleForm("mobile", e.target.value)}
            onInvalid={e => e.target.setCustomValidity("Any email will do.")}
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={4}>
          <MDBInput
            label="LRN"
            value={form.lrn}
            onChange={e => handleForm("lrn", e.target.value)}
            contrast={theme.dark}
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="my-3">
        <MDBCol size={6}>
          <MDBInput
            type="email"
            label="Parents E-mail Address"
            value={parents.email}
            required
            onChange={e => handleParents("email", e.target.value)}
            onInvalid={e => e.target.setCustomValidity("Any email will do.")}
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
        <MDBCol size={6}>
          <MDBInput
            label="Parents Mobile Number"
            value={parents.mobile}
            required
            onChange={e => handleParents("mobile", e.target.value)}
            onInvalid={e => e.target.setCustomValidity("Any email will do.")}
            onInput={e => e.target.setCustomValidity("")}
            contrast={theme.dark}
          />
        </MDBCol>
      </MDBRow>
      <MDBRow className="my-3">
        <MDBCol size={12}>
          <MDBInputGroup
            textBefore={<span className={theme.text}>Section</span>}
          >
            <select
              value={form.sectionId}
              onChange={e => handleForm("sectionId", e.target.value)}
              className={`form-control ${theme.bg} ${theme.text}`}
            >
              <option value={""}>Section</option>
              {sections.map(sec => {
                return (
                  <option key={sec._id} value={sec._id}>
                    {sec.name}
                  </option>
                );
              })}
            </select>
          </MDBInputGroup>
        </MDBCol>
      </MDBRow>
      <MDBRow className="my-3">
        <MDBCol size={12}>
          <div
            className="bg-image mx-auto hover-overlay"
            style={{ maxWidth: 200 }}
          >
            <img
              src={`${ENDPOINT}/assets/profile/${form.email}.jpg`}
              style={{ width: 200, height: 200 }}
              className="img-fluid"
              alt={form.username}
              onError={e => (e.target.src = PresetUser)}
            />
            <a href="#!">
              <div
                className="mask overlay d-flex align-items-center"
                style={{ backgroundColor: "rgba(57, 192, 237, 0.2)" }}
              >
                <input
                  type="file"
                  id="uploadProfile"
                  style={{ display: "none" }}
                  onChange={e => handleFile(e.target.files[0])}
                  accept="image/jpeg"
                />
                <MDBBtnGroup className="mx-auto" typeof="button">
                  <MDBTooltip
                    wrapperProps={{
                      color: "info",
                      size: "sm",
                      type: "button",
                      onClick: handleChangeProfile,
                    }}
                    title="Upload Avatar"
                  >
                    <MDBIcon icon="edit" />
                  </MDBTooltip>
                </MDBBtnGroup>
              </div>
            </a>
          </div>
        </MDBCol>
      </MDBRow>
    </>
  );
}
