import React, { useEffect } from "react";
import { MDBBadge, MDBListGroup, MDBListGroupItem } from "mdb-react-ui-kit";
import { formatCurrency } from "../../../../../components/utilities";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import Swal from "sweetalert2";
import { POS } from "../../../../../redux/slices/auth";

export default function ProductsList({
  products,
  setProducts,
  activeIndex,
  setActiveIndex,
}) {
  const { theme, pos } = useSelector(({ auth }) => auth),
    dispatch = useDispatch();

  const handleQuantity = async () => {
    if (products.length > 0) {
      const product = products[activeIndex];

      const { value: quantity } = await Swal.fire({
        title: `Edit quantity of ${product.name}`,
        input: "number",
        inputLabel: `${product.quantity} unit(s)`,
        inputPlaceholder: "Enter new quantity",
        inputValidator: value => {
          if (!value) {
            return "You need to write something!";
          }
        },
      });

      if (quantity) {
        const newArr = [...products];
        newArr[activeIndex].quantity = Number(quantity);
        setProducts(newArr);
      }
    } else {
      toast.warn("No available item to update.");
    }
    dispatch(POS(""));
  };

  const handlePrice = async () => {
    if (products.length > 0) {
      const product = products[activeIndex];

      const { value: price } = await Swal.fire({
        title: `Override price of ${product.name}`,
        input: "number",
        inputLabel: formatCurrency(product.price),
        inputPlaceholder: "Enter new price",
        inputValidator: value => {
          if (!value) {
            return "You need to write something!";
          }

          if (Number(value) === product.price) {
            return "Cannot override same price!";
          }
        },
      });

      if (price) {
        const newArr = [...products];
        if (!newArr[activeIndex].overridden) {
          newArr[activeIndex].overridden = newArr[activeIndex].price;
        }

        newArr[activeIndex].price = Number(price);
        setProducts(newArr);
      }
    } else {
      toast.warn("No available item to override.");
    }
    dispatch(POS(""));
  };

  const handleDelete = async () => {
    if (products.length > 0) {
      const product = products[activeIndex];

      Swal.fire({
        title: `Are you sure to delete`,
        html: product.name,
        confirmButtonText: "Yes, delete it!",
      }).then(result => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          const newArr = [...products];
          newArr.splice(activeIndex, 1);
          setProducts(newArr);
          toast.success(`${product.name} deleted!`);
        }
      });
    } else {
      toast.warn("No item to delete.");
    }
    dispatch(POS(""));
  };

  useEffect(() => {
    switch (pos) {
      case "f2":
        handleQuantity();
        break;

      case "f3":
        handlePrice();
        break;

      case "del":
        handleDelete();
        break;

      default:
        break;
    }
  }, [pos]);

  useEffect(() => {
    const handleKeyDown = async e => {
      var keyCode = e.keyCode || e.which;

      // f2
      if (keyCode === 113) {
        e.preventDefault();

        handleQuantity();
      }

      // f3
      if (keyCode === 114) {
        e.preventDefault();

        handlePrice();
      }

      //del
      if (keyCode === 46) {
        e.preventDefault();

        handleDelete();
      }

      //arrow up
      if (keyCode === 38) {
        e.preventDefault();

        if (activeIndex !== 0) {
          setActiveIndex(activeIndex - 1);
        }
      }

      //arrow down
      if (keyCode === 40) {
        e.preventDefault();

        if (activeIndex !== products.length - 1) {
          setActiveIndex(activeIndex + 1);
        }
      }
    };

    window.addEventListener("keydown", handleKeyDown);

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [activeIndex, products]);

  return (
    <MDBListGroup
      className="mt-2 overflow-auto mb-3 mb-md-0"
      style={{ minHeight: "400px" }}
    >
      {products.map((product, index) => (
        <MDBListGroupItem
          onClick={() => setActiveIndex(index)}
          key={`product-${index}`}
          className="d-flex justify-content-between align-items-start"
          style={{ maxHeight: "69px" }}
          color={activeIndex === index ? "primary" : theme.color}
        >
          <div className="ms-2 me-auto">
            <div className="fw-bold">{product.name}</div>
            {formatCurrency(product.price)} x {product.quantity} pcs
          </div>
          <MDBBadge pill color="info">
            {formatCurrency(product.price * product.quantity)}
          </MDBBadge>
        </MDBListGroupItem>
      ))}
    </MDBListGroup>
  );
}
