import React, { useState, useEffect } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBSpinner,
} from "mdb-react-ui-kit";
import { useDispatch, useSelector } from "react-redux";
import ModalForm from "./form";
import { REVERT, SAVE, UPDATE } from "../../../../../redux/slices/sections";

export default function SectionModal({
  setVisibility,
  visibility,
  data,
  setData,
}) {
  const { theme, token, auth } = useSelector(({ auth }) => auth),
    { isSuccess } = useSelector(({ sections }) => sections),
    [form, setForm] = useState({
      hasPermission: false,
      teacherId: auth._id,
      name: "",
    }),
    [loading, setLoading] = useState(false),
    dispatch = useDispatch();

  useEffect(() => {
    if (data && data._id) {
      setForm(data);
    }
  }, [data]);

  const toggleShow = () => setVisibility(!visibility);

  useEffect(() => {
    if (isSuccess) {
      if (data && data._id) {
        setData({});
      }
      dispatch(REVERT());
      setForm({
        name: "",
        hasPermission: false,
        teacherId: auth._id,
      });
      setLoading(false);
      if (visibility) {
        toggleShow();
      }
    }
  }, [isSuccess, data, visibility]);

  const handleForm = (name, value) => {
    setForm({
      ...form,
      [name]: value,
    });
  };

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);
    if (data && data._id) {
      dispatch(
        UPDATE({
          data: { ...form, teacherId: auth._id },
          id: data._id,
          token,
        })
      );
    } else {
      dispatch(SAVE({ form: { ...form, teacherId: auth._id }, token }));
    }
  };

  return (
    <MDBModal tabIndex="-1" show={visibility} staticBackdrop>
      <MDBModalDialog centered>
        <MDBModalContent className={`${theme.bg} ${theme.text}`}>
          <MDBModalHeader>
            <MDBModalTitle>
              {data._id ? "Update" : "Add"} a Section
            </MDBModalTitle>
          </MDBModalHeader>
          <form onSubmit={handleSubmit} autoComplete="off">
            <MDBModalBody>
              <ModalForm form={form} handleForm={handleForm} />
            </MDBModalBody>
            <MDBModalFooter className="py-0">
              <MDBBtn
                type="button"
                className="shadow-0"
                color={theme.color}
                onClick={() => {
                  if (data && data._id) {
                    setData({});
                  }
                  setForm({
                    hasPermission: false,
                    teacherId: auth._id,
                    name: "",
                  });
                  toggleShow();
                }}
                disabled={loading}
              >
                Close
              </MDBBtn>
              <MDBBtn type="submit" disabled={loading}>
                {loading ? <MDBSpinner size="sm" grow /> : "Submit"}
              </MDBBtn>
            </MDBModalFooter>
          </form>
        </MDBModalContent>
      </MDBModalDialog>
    </MDBModal>
  );
}
