import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import BreadCrumb from "../../../../components/breadcrumb";
import { MDBContainer, MDBRow, MDBCol, MDBInput } from "mdb-react-ui-kit";
import Pager from "../../../../components/pager";
import SectionsTable from "./table";
import { BROWSE } from "../../../../redux/slices/sections";
import SectionModal from "./modal";

const path = [
  {
    path: "Sections",
  },
];

const Sections = () => {
  const { token, theme, maxPage, auth } = useSelector(({ auth }) => auth),
    { list } = useSelector(({ sections }) => sections),
    [sections, setSections] = useState([]),
    [page, setPage] = useState(1),
    [totalPages, setTotalPages] = useState(1),
    [visibility, setVisibility] = useState(false),
    [data, setData] = useState({}),
    dispatch = useDispatch();

  useEffect(() => {
    if (auth.role) {
      if (auth.role.name !== "manager") {
        localStorage.removeItem("token");
        window.location.href = "/";
      }
    }
  }, [auth]);

  useEffect(() => {
    dispatch(BROWSE(token));
  }, [token]);

  useEffect(() => {
    if (data && data._id) {
      setVisibility(true);
    }
  }, [data]);

  useEffect(() => {
    if (sections.length > 0) {
      let totalPages = Math.floor(sections.length / maxPage);
      if (sections.length % maxPage > 0) totalPages += 1;
      setTotalPages(totalPages);

      if (page > totalPages) {
        setPage(totalPages);
      }
    }
  }, [sections, page, maxPage]);

  useEffect(() => {
    setSections(list);
  }, [list]);

  const handleSearch = string => {
    if (string) {
      setSections(
        list.filter(catalog =>
          catalog.name.toLowerCase().startsWith(string.toLowerCase())
        )
      );
    } else {
      setSections(list);
    }
  };

  return (
    <>
      <BreadCrumb
        title="Sections"
        paths={path}
        button={true}
        handleClick={() => setVisibility(true)}
      />
      <MDBContainer className="py-5 mt-4">
        <MDBRow className="mb-3">
          <MDBCol md={6}>
            <MDBInput
              onChange={e => handleSearch(e.target.value)}
              type="search"
              label="Search by Name"
              contrast={theme.dark}
            />
          </MDBCol>
          <Pager setPage={setPage} total={totalPages} page={page} />
        </MDBRow>
        <SectionsTable items={sections} page={page} setData={setData} />
      </MDBContainer>
      <SectionModal
        visibility={visibility}
        setVisibility={setVisibility}
        data={data}
        setData={setData}
      />
    </>
  );
};

export default Sections;
