import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBInput,
  MDBRow,
  MDBCol,
} from "mdb-react-ui-kit";
import { useDispatch, useSelector } from "react-redux";
import SearchTable from "./table";
import Pager from "../../../../../components/pager";
import axios from "axios";
import { toast } from "react-toastify";
import { POS } from "../../../../../redux/slices/auth";

export default function ProductSearch({
  visibility,
  setVisibility,
  handleData,
}) {
  const { theme, token, maxPage } = useSelector(({ auth }) => auth),
    [loading, setLoading] = useState(false),
    [page, setPage] = useState(1),
    [totalPages, setTotalPages] = useState(1),
    [products, setProducts] = useState([]),
    dispatch = useDispatch();

  const toggleShow = () => setVisibility(!visibility);

  useEffect(() => {
    if (products.length > 0) {
      let totalPages = Math.floor(products.length / maxPage);
      if (products.length % maxPage > 0) totalPages += 1;
      setTotalPages(totalPages);

      if (page > totalPages) {
        setPage(totalPages);
      }
    } else {
      setPage(1);
      setTotalPages(1);
    }
  }, [products, page, maxPage]);

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);

    const { key } = e.target;

    axios
      .get(`products/${key.value}/search`, {
        headers: {
          Authorization: `QTracy ${token}`,
        },
      })
      .then(res => {
        if (res && res.data) {
          setProducts(res.data);
          setLoading(false);
        }
      })
      .catch(err => {
        if (err.response?.data?.expired) {
          toast.warn("Account has been locked, contact your manager!");
          localStorage.removeItem("token");
          setTimeout(() => {
            window.location.href = "/";
          }, 5500);
        }
        toast.warn(err.response?.data?.error);
      });
  };

  return (
    <MDBModal tabIndex="-1" show={visibility} staticBackdrop>
      <MDBModalDialog centered size="lg">
        <MDBModalContent className={`${theme.bg} ${theme.text}`}>
          <MDBModalHeader>
            <MDBModalTitle>Search a product</MDBModalTitle>
          </MDBModalHeader>
          <MDBModalBody>
            <MDBRow>
              <MDBCol md={6}>
                <form onSubmit={handleSubmit} id="searchForm">
                  <MDBInput
                    type="search"
                    name="key"
                    label="Search by keyword"
                    contrast={theme.dark}
                    onInvalid={e =>
                      e.target.setCustomValidity("We need a keyword.")
                    }
                    onInput={e => e.target.setCustomValidity("")}
                    required
                  />
                </form>
              </MDBCol>
              <Pager setPage={setPage} total={totalPages} page={page} />
            </MDBRow>
            <SearchTable
              items={products}
              page={page}
              setData={handleData}
              loading={loading}
            />
          </MDBModalBody>
          <MDBModalFooter className="py-0">
            <MDBBtn
              type="button"
              className="shadow-0"
              color={theme.color}
              onClick={() => {
                document.getElementById("searchForm").reset();
                if (products.length > 0) {
                  setProducts([]);
                }
                toggleShow();
                dispatch(POS(""));
              }}
              disabled={loading}
            >
              Close
            </MDBBtn>
          </MDBModalFooter>
        </MDBModalContent>
      </MDBModalDialog>
    </MDBModal>
  );
}
