import React from "react";
import { MDBBtn, MDBIcon, MDBTooltip } from "mdb-react-ui-kit";

export default function TableCard({ item, setData }) {
  return (
    <tr className="text-center">
      <td className="text-start">
        <p className="mb-0 text-capitalize">{item.name}</p>
      </td>
      <td>
        <p className="mb-0">{item.barcode}</p>
      </td>
      <td className="text-center">
        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title="Add to sale!"
        >
          <MDBBtn onClick={() => setData(item)} size="sm">
            <MDBIcon icon="share" />
          </MDBBtn>
        </MDBTooltip>
      </td>
    </tr>
  );
}
