import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import BreadCrumb from "../../../../components/breadcrumb";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBInputGroup,
} from "mdb-react-ui-kit";
import Pager from "../../../../components/pager";
import EmployeeTable from "./table";
import { BROWSE } from "../../../../redux/slices/users";
import ProductModal from "./modal";
import { fullName } from "../../../../components/utilities";
import { useParams } from "react-router-dom";

const path = [
  {
    path: "Students",
  },
];

const Students = () => {
  const { token, theme, maxPage, auth } = useSelector(({ auth }) => auth),
    { catalogs } = useSelector(({ users }) => users),
    { list } = useSelector(({ sections }) => sections),
    [students, setStudents] = useState([]),
    [page, setPage] = useState(1),
    [totalPages, setTotalPages] = useState(1),
    [visibility, setVisibility] = useState(false),
    [data, setData] = useState({}),
    { type } = useParams(),
    dispatch = useDispatch();

  useEffect(() => {
    if (auth.role) {
      if (auth.role.name !== "manager") {
        localStorage.removeItem("token");
        window.location.href = "/";
      }
    }
  }, [auth]);

  useEffect(() => {
    dispatch(BROWSE({ token, type }));
  }, [token, type]);

  useEffect(() => {
    if (data && data._id) {
      setVisibility(true);
    }
  }, [data]);

  useEffect(() => {
    if (students.length > 0) {
      let totalPages = Math.floor(students.length / maxPage);
      if (students.length % maxPage > 0) totalPages += 1;
      setTotalPages(totalPages);

      if (page > totalPages) {
        setPage(totalPages);
      }
    }
  }, [students, page, maxPage]);

  useEffect(() => {
    if (catalogs.length > 0) {
      setStudents(catalogs);
    }
  }, [catalogs]);

  const handleSearch = string => {
    if (string) {
      setStudents(
        catalogs.filter(catalog =>
          fullName(catalog.fullName)
            .toLowerCase()
            .startsWith(string.toLowerCase())
        )
      );
    } else {
      setStudents(catalogs);
    }
  };

  const handleSection = string => {
    if (string) {
      setStudents(catalogs.filter(catalog => catalog.sectionId === string));
    } else {
      setStudents(catalogs);
    }
  };
  return (
    <>
      <BreadCrumb
        title="Student list"
        paths={path}
        button={true}
        handleClick={() => setVisibility(true)}
      />
      <MDBContainer className="py-5 mt-4">
        <MDBRow className="mb-3">
          <MDBCol md={6}>
            <MDBInput
              onChange={e => handleSearch(e.target.value)}
              type="search"
              label="Search by Fullname"
              contrast={theme.dark}
            />
          </MDBCol>
          <MDBCol md={4}>
            <MDBInputGroup
              textBefore={<span className={theme.text}>Section</span>}
            >
              <select
                onChange={e => handleSection(e.target.value)}
                className={`form-control ${theme.text} ${theme.text} text-capitalize`}
              >
                <option value={""}>Section</option>
                {list.map(sec => {
                  return (
                    <option
                      key={sec._id}
                      className="text-capitalize"
                      value={sec._id}
                    >
                      {sec.name}
                    </option>
                  );
                })}
              </select>
            </MDBInputGroup>
          </MDBCol>
          <Pager setPage={setPage} total={totalPages} page={page} />
        </MDBRow>
        <EmployeeTable items={students} page={page} setData={setData} />
      </MDBContainer>
      <ProductModal
        list={list}
        visibility={visibility}
        setVisibility={setVisibility}
        data={data}
        setData={setData}
      />
    </>
  );
};

export default Students;
