import {
  MDBBadge,
  MDBCol,
  MDBInput,
  MDBListGroup,
  MDBListGroupItem,
} from "mdb-react-ui-kit";
import React from "react";
import { fullName } from "../utilities";

export default function list({ items }) {
  return (
    <MDBCol md={3} size={4} className="overflow-auto h-100 px-0 hidden-scroll">
      <MDBListGroup>
        {items.map(item => {
          return (
            <MDBListGroupItem
              key={item._id}
              color="light"
              action
              className="d-flex justify-content-between align-items-start"
            >
              <div className="ms-2 me-auto">
                <div className="fw-bold">
                  {fullName(item?.userId?.fullName)}
                </div>
                {item?.userId?.role?.display_name}
              </div>
              <MDBBadge pill color={"success"}>
                {item?.in}
              </MDBBadge>
              <MDBBadge pill color={"danger"}>
                {item?.out}
              </MDBBadge>
            </MDBListGroupItem>
          );
        })}
      </MDBListGroup>
    </MDBCol>
  );
}
