import React, { useEffect, useState } from "react";
import { MDBCol, MDBContainer, MDBRow, MDBTypography } from "mdb-react-ui-kit";
import { useDispatch, useSelector } from "react-redux";
import BarcodeScanner from "./scanner";
import ProductsList from "./products";
import { toast } from "react-toastify";
import TransactionRemarks from "./remarks";
import { formatCurrency } from "../../../../components/utilities";
import Swal from "sweetalert2";
import axios from "axios";
import ProductSearch from "./modal";
import { POS } from "../../../../redux/slices/auth";
import { Beforeunload } from "react-beforeunload";

const Cashier = () => {
  const { auth, token, pos, theme } = useSelector(state => state.auth),
    [search, setSearch] = useState(false),
    [activeIndex, setActiveIndex] = useState(0),
    [products, setProducts] = useState([]),
    [remarks, setRemarks] = useState(""),
    [total, setTotal] = useState(0),
    dispatch = useDispatch();

  useEffect(() => {
    if (auth.role) {
      if (auth.role.name !== "cashier") {
        localStorage.removeItem("token");
        window.location.href = "/";
      }
    }
  }, [auth]);

  const handleTransaction = async () => {
    if (products.length > 0) {
      const total = products.reduce(
        (total, product) => total + product.price * product.quantity,
        0
      );

      const { value: cash } = await Swal.fire({
        title: "Customer cash amount",
        input: "number",
        inputLabel: `Total is ${formatCurrency(total)}`,
        inputValidator: value => {
          if (!value) {
            return "You need to write something!";
          }

          if (Number(value) < total) {
            return "Insufficient cash!";
          }
        },
      });

      if (cash) {
        if (cash > total) {
          Swal.fire({
            icon: "info",
            title: `Please return ${formatCurrency(cash - total)}`,
            html: "Giving back change starts with you.",
            showConfirmButton: false,
            timer: 1500,
          });
        } else {
          Swal.fire({
            icon: "info",
            title: "Always thank a customer for paying exact amount.",
            html: "Appreciate the small things.",
            showConfirmButton: false,
            timer: 1500,
          });
        }
        const form = {
          cashierId: auth._id,
          total,
          remarks,
          cash,
          products,
          quantity: products.reduce(
            (total, product) => total + product.quantity,
            0
          ),
        };

        axios
          .post("sales/save", form, {
            headers: {
              Authorization: `QTracy ${token}`,
            },
          })
          .then(item => {
            window.open(
              `/receipt/${item.data?._id}/print`,
              "Receipt",
              "top=100px,width=500px,height=650px"
            );
            toast.success("Transaction complete!");
            setProducts([]);
            setRemarks("");
            setActiveIndex(0);
          })
          .catch(err => {
            toast.error(err.response?.data?.error);
          });
      }
    } else {
      toast.warn("No item to transact.");
    }
    dispatch(POS(""));
  };

  const handleReset = async () => {
    if (products.length > 0) {
      Swal.fire({
        title: `Are you sure to reset transaction?`,
        html: "This action cannot be undone",
        confirmButtonText: "Yes, reset it!",
      }).then(result => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          setProducts([]);
          setRemarks("");
          setActiveIndex(0);
        }
      });
    } else {
      toast.warn("No transaction to reset!");
    }
    dispatch(POS(""));
  };

  useEffect(() => {
    switch (pos) {
      case "f1":
        document.getElementById("barcode").focus();
        break;

      case "f4":
        handleTransaction();
        break;

      case "f7":
        handleReset();
        break;

      case "f9":
        setSearch(true);
        break;

      default:
        break;
    }
  }, [pos]);

  useEffect(() => {
    const barcode = document.getElementById("barcode"),
      remark = document.getElementById("remarks");

    const handleKeyDown = async e => {
      var keyCode = e.keyCode || e.which;

      //arrow left
      if (keyCode === 37) {
        e.preventDefault();

        barcode.focus();
      }

      //arrow right
      if (keyCode === 39) {
        e.preventDefault();

        remark.focus();
      }

      // f1
      if (keyCode === 112) {
        e.preventDefault();

        barcode.focus();
      }

      // f4
      if (keyCode === 115) {
        e.preventDefault();

        handleTransaction();
      }

      // f7
      if (keyCode === 118) {
        e.preventDefault();

        handleReset();
      }

      // f9
      if (keyCode === 120) {
        e.preventDefault();

        setSearch(true);
      }
    };

    window.addEventListener("keydown", handleKeyDown);

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [products]);

  useEffect(() => {
    if (products.length > 0) {
      setTotal(
        products.reduce(
          (total, product) => total + product.price * product.quantity,
          0
        )
      );
    } else {
      setTotal(0);
    }
  }, [products, total]);

  useEffect(() => {
    if (activeIndex > products.length - 1) {
      setActiveIndex(products.length - 1);
    }
  }, [activeIndex, products]);

  const handleProduct = product => {
    const newArr = [...products];
    var newObj = { ...product };
    newObj.id = newObj._id;

    const existing = newArr.findIndex(item => item.id === newObj.id);

    if (existing < 0) {
      delete newObj._id;
      newObj.productId = product._id;
      newObj.quantity = 1;
      newArr.push(newObj);
      setActiveIndex(newArr.length - 1);
    } else {
      newArr[existing].quantity += 1;
      setActiveIndex(existing);
    }

    setProducts(newArr);
  };

  return (
    <MDBContainer fluid>
      {products.length > 0 && (
        <Beforeunload
          onBeforeunload={() => "Current transaction will be gone!"}
        />
      )}
      <MDBRow className="mt-1">
        <MDBCol md={8}>
          <BarcodeScanner handleProduct={handleProduct} />
          <ProductsList
            products={products}
            setProducts={setProducts}
            activeIndex={activeIndex}
            setActiveIndex={setActiveIndex}
          />
        </MDBCol>
        <MDBCol md={4}>
          <TransactionRemarks remarks={remarks} setRemarks={setRemarks} />
          <MDBTypography
            className={`mb-0 my-3 d-flex align-items-center justify-content-between ${theme.text}`}
            tag="h3"
          >
            <span>Total Items:</span>
            <span>
              {products.reduce((total, product) => total + product.quantity, 0)}
            </span>
          </MDBTypography>
          <MDBTypography
            className={`mb-0 my-3 d-flex align-items-center justify-content-between ${theme.text}`}
            tag="h3"
          >
            <span>Total Amount:</span>
            <span>{formatCurrency(total)}</span>
          </MDBTypography>
        </MDBCol>
      </MDBRow>
      <ProductSearch
        visibility={search}
        setVisibility={setSearch}
        handleData={handleProduct}
      />
    </MDBContainer>
  );
};

export default Cashier;
