import React, { useEffect } from "react";
import {
  MDBContainer,
  MDBTable,
  MDBTableHead,
  MDBTableBody,
  MDBTypography,
} from "mdb-react-ui-kit";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { FIND } from "../../redux/slices/sales";
import ReceiptHeader from "./header";
import TableCard from "./card";
import { formatCurrency, fullName } from "../utilities";

export default function Receipt() {
  const { token } = useSelector(({ auth }) => auth),
    { record } = useSelector(({ sales }) => sales),
    dispatch = useDispatch(),
    { id, action } = useParams();

  useEffect(() => {
    if (action === "print") {
      if (record && record.items) {
        window.print();
        window.close();
      }
    }
  }, [action, record]);

  useEffect(() => {
    if (id) {
      dispatch(FIND({ id, token }));
    }
  }, [id]);

  return (
    <MDBContainer
      style={{
        fontFamily: "arial",
        fontSize: "15px",
        width: action === "print" ? "250px" : "100%",
        maxWidth: action === "print" ? "250px" : "600px",
        letterSpacing: "-0.5px",
        wordSpacing: "-1px",
        lineHeight: "20px",
      }}
      className="text-center text-black"
    >
      <ReceiptHeader />
      <MDBTable
        style={{
          fontSize: "15px",
          letterSpacing: "-0.5px",
          wordSpacing: "-1px",
        }}
        align="middle"
        hover
        responsive
        color="light"
        className={`mb-0 ${action !== "print" && "mt-3"}`}
        small
        borderless
      >
        <caption className="caption-top pb-1 pt-3 mx-0 text-black border-bottom border-black fw-bold text-center">
          -{id}-
        </caption>
        {record.remarks && (
          <caption className="py-1 text-black border-bottom border-black fw-bold">
            {record.remarks}
          </caption>
        )}

        <MDBTableHead className="border-bottom border-black">
          <tr>
            <th scope="col" className="text-start p-1 fw-bold">
              Items
            </th>
            <th scope="col" className="p-1 fw-bold text-start">
              Qty
            </th>
            {action !== "print" && (
              <th scope="col" className="fw-bold p-1">
                Price
              </th>
            )}
            <th scope="col" className="p-1 fw-bold text-end">
              {action === "print" ? "Price" : "Total"}
            </th>
          </tr>
        </MDBTableHead>
        <MDBTableBody className="border-bottom border-black">
          {record.items?.map((item, index) => (
            <TableCard key={`receipt-${index}`} item={item} action={action} />
          ))}
          <tr className="border-bottom border-black fw-bold">
            <td className="text-start px-1 py-0">Total</td>
            <td
              colSpan={action === "print" ? 1 : 2}
              className="px-1 py-0 text-start"
            >
              {record.quantity}
            </td>
            <td className="px-1 py-0 text-end">
              {formatCurrency(record.total)}
            </td>
          </tr>
          <tr className="fw-bold">
            <td
              colSpan={action === "print" ? 2 : 3}
              className="text-start px-1 py-0"
            >
              Cash
            </td>
            <td className="px-1 py-0 text-end">
              {formatCurrency(record.cash)}
            </td>
          </tr>
          <tr className="fw-bold">
            <td
              colSpan={action === "print" ? 2 : 3}
              className="text-start px-1 py-0"
            >
              Change
            </td>
            <td className="px-1 py-0 text-end">
              {formatCurrency(record.cash - record.total)}
            </td>
          </tr>
        </MDBTableBody>
      </MDBTable>
      <MDBTypography
        style={{ marginBottom: "-0.5%" }}
        className="text-capitalize text-start"
      >
        {fullName(record.cashierId?.fullName)}
      </MDBTypography>
      <MDBTypography
        style={{ marginBottom: "-0.5%" }}
        className="d-flex justify-content-end"
      >
        {new Date(record.createdAt).toLocaleString()}
      </MDBTypography>
      <MDBTypography
        style={{ marginBottom: "-0.5%" }}
        className="text-center mt-2"
      >
        THIS RECEIPT SHALL BE VALID FOR <b>FIVE(5) YRS</b> FROM THE DATE OF THE
        PERMIT TO USE.
      </MDBTypography>
      <MDBTypography className="text-center mt-2">
        <strong>THANK YOU, COME AGAIN.</strong>
      </MDBTypography>
    </MDBContainer>
  );
}
