import React from "react";
import { MDBBtn, MDBIcon, MDBTooltip } from "mdb-react-ui-kit";
import Swal from "sweetalert2";
import {
  ENDPOINT,
  fullMobile,
  fullName,
  getAge,
  PresetUser,
} from "../../../../../../components/utilities";
import { useDispatch, useSelector } from "react-redux";
import { DESTROY, UPDATE } from "../../../../../../redux/slices/users";

export default function TableCard({ item, setData }) {
  const { token } = useSelector(({ auth }) => auth),
    dispatch = useDispatch();

  const handleArchives = () => {
    const action = item.deletedAt ? "unlock" : "lock";

    Swal.fire({
      icon: "question",
      title: `Do you want to ${action} this?`,
      html: "A locked account cannot be logged in.",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then(result => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        if (item.deletedAt) {
          dispatch(
            UPDATE({
              data: { deletedAt: "" },
              id: item._id,
              token,
            })
          );
        } else {
          dispatch(DESTROY({ id: item._id, token }));
        }
      }
    });
  };

  const handleDTR = () =>
    window.open(
      `/attendance/${item._id}`,
      "Attendance",
      "top=100px,width=768px,height=650px"
    );

  const handleQr = () =>
    window.open(
      `/qr/${item.email}/${item._id}`,
      "QR",
      "top=400px,width=500px,height=300px"
    );

  return (
    <tr className="text-center">
      <td className="text-start">
        <p className="fw-bold mb-1 text-capitalize">
          <img
            src={`${ENDPOINT}/assets/profile/${item.email}.jpg`}
            alt=""
            style={{ width: "45px", height: "45px" }}
            className="rounded-circle"
            onError={e => (e.target.src = PresetUser)}
          />
          <MDBTooltip
            tag="span"
            wrapperClass="d-inline-block"
            title={item.isMale ? "Male" : "Female"}
          >
            <MDBIcon
              className="me-1"
              color={item.isMale ? "primary" : "danger"}
              icon={item.isMale ? "male" : "female"}
            />
          </MDBTooltip>
          {fullName(item.fullName)} {item.nickname && `(${item.nickname})`}
        </p>
        <p className="text-muted mb-0">
          {new Date(item.dob).toDateString()},&nbsp;{getAge(item.dob)}yrs old
        </p>
      </td>
      <td>
        <p className="fw-bold mb-1">{item.email}</p>
        <p className="text-muted mb-0">{fullMobile(item.mobile)}</p>
      </td>
      <td>
        <p className="mb-0">{item.lrn}</p>
      </td>
      <td className="text-center">
        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title="Get QR code!"
        >
          <MDBBtn onClick={() => handleQr()} size="sm" color="warning">
            <MDBIcon icon="qrcode" />
          </MDBBtn>
        </MDBTooltip>
        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title="Show daily time in!"
        >
          <MDBBtn onClick={() => handleDTR()} size="sm" color="success">
            <MDBIcon icon="calendar-check" />
          </MDBBtn>
        </MDBTooltip>
        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title="Update product!"
        >
          <MDBBtn onClick={() => setData(item)} size="sm" color="info">
            <MDBIcon icon="pen" />
          </MDBBtn>
        </MDBTooltip>
        <MDBTooltip
          tag="span"
          wrapperClass="d-inline-block"
          title={`${item.deletedAt ? "Unlock" : "Lock"} account!`}
        >
          <MDBBtn
            size="sm"
            color={item.deletedAt ? "primary" : "danger"}
            onClick={handleArchives}
          >
            <MDBIcon icon={`${item.deletedAt ? "lock-open" : "lock"}`} />
          </MDBBtn>
        </MDBTooltip>
      </td>
    </tr>
  );
}
