import React from "react";
import { MDBInput } from "mdb-react-ui-kit";
import { useSelector } from "react-redux";
import axios from "axios";
import { toast } from "react-toastify";

export default function BarcodeScanner({ handleProduct }) {
  const { theme, token } = useSelector(({ auth }) => auth);

  const handleSubmit = e => {
    e.preventDefault();

    const { barcode } = e.target;

    axios
      .get(`products/${barcode.value}/find`, {
        headers: {
          Authorization: `QTracy ${token}`,
        },
      })
      .then(res => {
        handleProduct(res.data);
      })
      .catch(err => {
        if (err.response?.data?.expired) {
          toast.warn("Account has been locked, contact your manager!");
          localStorage.removeItem("token");
          setTimeout(() => {
            window.location.href = "/";
          }, 5500);
        }
        toast.warn(err.response?.data?.error);
      });

    e.target.reset();
  };

  return (
    <form onSubmit={handleSubmit} autoComplete="off">
      <MDBInput
        id="barcode"
        name="barcode"
        label="Barcode scanner"
        contrast={theme.dark}
        required
        autoFocus
      />
    </form>
  );
}
